import React from "react";
import { Button, Menu, MenuItem, Typography } from "@mui/material";
import LogoutIcon from '@mui/icons-material/Logout';
import LoopIcon from '@mui/icons-material/Loop';
import { useApp } from "../contexts/AppContext";
import './_AccountSummary.scss';
import { logout } from "../apis/AuthApi";
import { formatNumber } from "../lib/NumberUtils";

export function AccountSummary() {
  const { balance, user, authenticate } = useApp();
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);
  const handleClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleLogout = async () => {
    setAnchorEl(null);
    try {
      await logout();
      authenticate(null);
    } catch (error) {

    }
  }

  return user ? (
    <div className="account-summary">
      <button>
        <LoopIcon />
      </button>
      <Button
        aria-expanded={open ? 'true' : undefined}
        onClick={handleClick}
      >
        <small>{user.email}</small>
        <span className="balance">{formatNumber(parseFloat((balance || 0)?.toString()), 2)}$</span>
      </Button>
      <Menu
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'right',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
        PaperProps={{
          className: 'account-summary-list',
          style: {
            width: 200,
          },
        }}
      >
        <MenuItem onClick={handleLogout}>
          <LogoutIcon />
          <Typography ml={1}>ng xut</Typography>
        </MenuItem>
      </Menu>
    </div>
  )
    : null;
}