import { useState, useEffect } from "react";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import IconButton from "@mui/material/IconButton";
import MenuIcon from "@mui/icons-material/Menu";
import { Link, useLocation } from "react-router-dom";

import BaseSvgIcon from "../base/BaseSvgIcon";
import { AccountSummary } from "./_AccountSummary";
import { useSettings } from "../contexts/SettingsContext";
import { useApp } from "../contexts/AppContext";
import "./_Header.scss";
import TokenSelect from "../components/TokenSelect/TokenSelect";

export default function Header() {
  const location = useLocation();
  const { openAuthDialog } = useSettings();
  const { isAuthenticated } = useApp();
  const [isTrading, setIsTrading] = useState(false);

  useEffect(() => {
    setIsTrading(location.pathname == "/trading");
  }, [location]);

  return (
    <Box sx={{ flexGrow: 1 }}>
      <AppBar position="fixed" className="appbar">
        <Toolbar>
          <IconButton
            size="large"
            edge="start"
            color="inherit"
            aria-label="menu"
            sx={{ mr: 2 }}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" component="div">
            <Link to={"/"} id="logo-text">
              <img src="/images/logo.png" alt="logo" />
              uarax
            </Link>
          </Typography>

          {isTrading && <TokenSelect />}

          <div className="actions">
            {isAuthenticated ? (
              <AccountSummary />
            ) : (
              <button className="signup-btn" onClick={openAuthDialog}>
                <BaseSvgIcon iconName="sign-l2" size={20}></BaseSvgIcon>
                Sign up
              </button>
            )}
          </div>
        </Toolbar>
      </AppBar>
    </Box>
  );
}
